// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
//import the vue instance
import Vue from 'vue'
//import the App component
import App from './App'
//import the vue router
import VueRouter from 'vue-router'
//tell vue to use the router
// Vue.use(VueRouter)
/* eslint-disable no-new */
//import the hello component
import iView from 'iview';
//import './iview.css';

//import the hello component
import Hello from './components/Hello'
//import the about component
import About from './components/About'

import Business from './components/Business'
import Art from './components/Art'
import ArtList from './components/ArtList'
import BusinessList from './components/BusinessList'


//define your routes
const routes = [
	//route for the home route of the webpage
	{ path: '/', component: Hello },
	//route for the about route of the webpage
	{ path: '/about', component: About },

	{ path: '/business/editor', component: Business },
	{ path: '/business/list', component: BusinessList },
	{ path: '/art/editor', component: Art },
	{ path: '/art/list', component: ArtList },

	//{ path: '/survey/subject/list', component: Subjectlist },
]

// Create the router instance and pass the `routes` option
// You can pass in additional options here, but let's
// keep it simple for now.
const router = new VueRouter({
  routes, // short for routes: routes
  mode: 'history'
})
/*
* global component
*
* */
Vue.use(VueRouter);
Vue.use(iView);

//instatinat the vue instance
new Vue({
	//define the selector for the root component
  el: '#app',
  //pass the template to the root component
  template: '<App/>',
  //declare components that the root component can access
  components: { App },
  //pass in the router to the vue instance
  router
}).$mount('#app');//mount the router on the app



