<template>
    <section class="main-content-wrapper">
        <section id="main-content">
            <div id="subject">

                <h3>???????????????</h3>

                <span class="_label">????????????</span>
                <AutoComplete
                        v-model="business"
                        :data="businessName"
                        @on-search="handleSearch"
                        placeholder="input here"
                        style="width:300px">
                </AutoComplete>

                <div class="_br"></div>
                <span class="_label">???&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;???</span>
                <Input v-model="video" placeholder="??????..." style="width: 50%"></Input>

                <div class="_br"></div>
                <span class="_label">???&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;???</span>
                <Input v-model="title" placeholder="??????..." style="width: 50%"></Input>

                <div class="_br"></div>
                <span class="_label">???&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;???</span>
                <Input v-model="material" placeholder="??????..." style="width: 60%"></Input>

                <div class="_br"></div>
                <span class="_label">???&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;???</span>
                <Input v-model="weight" placeholder="??????..." style="width: 10%"></Input>
                <span class="_label">/???</span>

                <div class="_br"></div>
                <span class="_label">???&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;???</span>
                <Input v-model="size" placeholder="??????..." style="width: 20%"></Input>

                <div class="_br"></div>
                <span class="_label">???&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;???</span>
                <Input v-model="mark" placeholder="??????..." style="width: 60%"></Input>

                <div class="_br"></div>
                <span class="_label">????????????</span>
                <DatePicker type="date" v-model="createDate" placeholder="Select date"
                            style="width: 200px"></DatePicker>

                <div class="_br"></div>

                <span class="_label" style="font-weight: bold">????????????</span>
                <div class="_br"></div>
                <div class="_br"></div>
                <Input v-model="tempImgMark" placeholder="??????????????????..."
                       style="margin-right:10px;margin-left: 63px;float: left;width: 200px"></Input>
                <div style="    color: rgb(26, 188, 155);
    position: absolute;
    margin-top: 35px;
    margin-left: 63px;">
                    <ul>
                        <li v-for="item in Shape">explain:{{item.name}} ,img:{{item.id}}</li>
                    </ul>
                </div>
                <Upload style="margin-left: 60px;display: inline-block;"
                        :on-success="handleSuccess"
                        :on-remove="handleRemove"
                        action="/index/upload">
                    <Button type="ghost" icon="ios-cloud-upload-outline">Upload files</Button>
                </Upload>


                <div class="_br"></div>
                <div class="_br"></div>
                <Button type="primary" v-on:click="submit" style="margin: 10px 0 10px 60px;">????????????</Button>

            </div>
        </section>
    </section>
</template>
<script>
    /*
     *
     *
     * */
    export default {
      name      : 'Business',
      data() {
        return {
          businessName: [],
          businessNick: '',
          business    : '',

          video      : '',
          title      : '',
          material   : '',
          weight     : '',
          size       : '',
          mark       : '',
          createDate : '',
          tempImgMark: '',
          // ????????????
          Shape      : [],
          host       : 'http://pba2392po.bkt.clouddn.com/',
        };
      },
      mounted() {

        var that  = this;
        var sear  = location.search.replace('?', '').split('&');
        var oSear = {};

        for (var i = 0; sear[i]; i++) {
          var o = sear[i].split('=');

          oSear[o[0]] = o[1];
        }

        if (!oSear['id'])
          return;

        $.ajax({
          url    : '/art/fetchdetail',
          type   : 'GET',
          data   : {
            id: oSear['id'],
          },
          success: function(res) {

            var data = JSON.parse(res);

            if (data.success - 0 === 1) {

              for (var k in data.data) {
                that[k] = data.data[k];
              }
              that.business = [that.businessId];
            }
          },
        });
      },
      props     : {},
      components: {},
      methods   : {
        handleSearch: function(value) {

          var that = this;

          $.ajax({
            url    : '/business/fetchlist',
            type   : 'GET',
            data   : {
              keyWord: value,
            },
            success: function(res) {

              var data = JSON.parse(res);

              if (data.success - 0 === 1) {

                  /*
                   * format data
                   *
                   * */
                var aList = [];

                data.list.forEach(function(item) {
                  var str = item.name + '__' + item.id;

                  aList.push(str);
                });

                that.businessName = aList;
              }
            },
          });

        },
        add         : function() {
        },
        del         : function() {
        },
        submit      : function() {
          var sear  = location.search.replace('?', '').split('&');
          var oSear = {};

          for (var i = 0; sear[i]; i++) {
            var o = sear[i].split('=');

            oSear[o[0]] = o[1];
          }

          var that        = this;
          var aBusinessId = that.business.split('__');
          var type        = oSear['editor'] ? 'update' : 'add';
          var sId         = oSear['editor'] ? oSear['id'] : +new Date;

            /*
             *
             * check
             * */
          if (!this.title) {
            this.$Message.error({
              content : '??????????????????',
              duration: 2.6,
            });
            return;
          }
          if (!this.material) {
            this.$Message.error({
              content : '??????????????????',
              duration: 2.6,
            });
            return;
          }
          if (!this.weight) {
            this.$Message.error({
              content : '??????????????????',
              duration: 2.6,
            });
            return;
          }
          if (!this.size) {
            this.$Message.error({
              content : '??????????????????',
              duration: 2.6,
            });
            return;
          }
          if (!this.mark) {
            this.$Message.error({
              content : '??????????????????',
              duration: 2.6,
            });
            return;
          }

          $.ajax({
            url    : '/art/' + type,
            type   : 'POST',
            data   : {
              _id         : sId,
              Video       : this.video,
              Title       : this.title,
              Material    : this.material,
              Weight      : this.weight,
              Size        : this.size,
              Mark        : this.mark,
              CreateDate  : this.createDate || new Date,
              // ????????????
              Shape       : JSON.stringify(this.Shape),
              BusinessId  : aBusinessId.pop(),
              businessNick: aBusinessId.shift(),
            },
            success: function(res) {

              if (JSON.parse(res).ok - 0 === 1) {

                that.$Message.loading({
                  content : '????????????',
                  duration: 2.6,
                });
              }
            },
          });
        },
        handleSuccess(res, file) {

          var o = {
            id  : res.key,
            name: this.tempImgMark,
            url : this.host + res.key,
          };

          this.Shape.push(o);
          console.log(this.Shape);
        },
        handleRemove: function(item) {
          var that = this;
          this.Shape.forEach(function(e, i) {
            if (e.id === item.name)
              that.Shape.splice(i, 1);
          });
        },
      },
    };
</script>
<!-- styling for the component -->
<style>
    .option {
        border: 1px solid #e4e6e8;
        margin: 10px 0;
        padding: 5px 10px;
        border-radius: 2px;
    }

    .option-content li {
        padding: 7px 0;
        list-style: none;
    }

    .option-content li > i {
        font-style: normal;
        color: #1abc9b;
        margin-right: 5px;

    }

    ._br {
        height: 10px;
    }

    ._label {
        margin-right: 10px;
    }

    h3 {
        font-size: 14px;
        margin: 0;
        padding: 0 0 15px 0;
    }
</style>


