<template>
    <section class="main-content-wrapper">
        <section id="main-content">
            <div id="subject">

                <h3>????????????</h3>

                <span class="_label">???&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;???</span>
                <Input v-model="name" placeholder="????????????..." style="width: 50%"></Input>

                <div class="_br"></div>
                <span class="_label">????????????</span>
                <i-select v-model="subjectType" style="width:200px">
                    <Option v-for="item in optionList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                </i-select>

                <div class="_br"></div>

                <span class="_label">???&nbsp;???&nbsp;&nbsp;???</span>
                <Input v-model="contacts" placeholder="???????????????..." style="width: 20%"></Input>
                <div class="_br"></div>

                <span class="_label">???&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;???</span>
                <Input v-model="number" placeholder="????????????..." style="width: 20%"></Input>
                <div class="_br"></div>

                <span class="_label">???&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;???</span>
                <Input v-model="address" placeholder="????????????..." style="width: 60%"></Input>

                <div class="_br"></div>
                <span class="_label">????????????</span>
                <DatePicker type="date" v-model="createDate" placeholder="Select date"
                            style="width: 200px"></DatePicker>

                <div class="_br"></div>
                <span class="_label">???&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;???</span>
                <Input v-model="mark" placeholder="????????????..." style="width: 60%"></Input>

                <div class="_br"></div>

                <span class="_label">????????????</span>
                <div class="_br"></div>
                <div class="_br"></div>
                <Upload style="margin-left: 60px;"
                        :on-success="handleSuccess"
                        :show-upload-list="true"
                        action="/index/upload" v-model="businessPic">
                    <Button type="ghost" icon="ios-cloud-upload-outline">Upload files</Button>
                </Upload>

                <Button type="primary" v-on:click="submit" style="margin: 10px 0 10px 60px;">????????????</Button>

            </div>
        </section>
    </section>
</template>
<script>
    /*
     {
     Name       :'',
     Address    :'',
     Contacts   :'',
     Number     :'',
     CreateDate :'',
     Mark       :'',
     BusinessPic:'',
     IsFaithful : '0'
     }*/

    export default {
      name      : 'Business',
      data() {
        return {
          name       : '',
          address    : '',
          contacts   : '',
          number     : '',
          createDate : '',
          mark       : '',
          businessPic: '',
          optionList : [
            {
              value: '1',
              label: '???',
            },
            {
              value: '2',
              label: '??????',
            },
          ],
          subjectType: '2',
          host       : '//pba2392po.bkt.clouddn.com/',
        };
      },
      mounted() {

        var that  = this;
        var sear  = location.search.replace('?', '').split('&');
        var oSear = {};

        for (var i = 0; sear[i]; i++) {
          var o = sear[i].split('=');

          oSear[o[0]] = o[1];
        }

        if (!oSear['id'])
          return;

        $.ajax({
          url    : '/business/fetchdetail',
          type   : 'GET',
          data   : {
            id: oSear['id'],
          },
          success: function(res) {

            var data = JSON.parse(res);

            if (data.success - 0 === 1) {

              for (var k in data.data) {
                that[k] = data.data[k];
              }
            }
          },
        });
      },
      props     : {},
      components: {},
      methods   : {
        handleUpload(file) {
          this.file = file;
          return false;
        },
        add   : function() {
        },
        del   : function() {
        },
        submit: function() {
          var sear  = location.search.replace('?', '').split('&');
          var oSear = {};

          for (var i = 0; sear[i]; i++) {
            var o = sear[i].split('=');

            oSear[o[0]] = o[1];
          }

          var that = this;
          var type = oSear['editor'] ? 'update' : 'add';
          var sId  = oSear['editor'] ? oSear['id'] : this.number.substr(-4) + (+new Date
          );
          /*
          * check
          *
          * */
          if(!this.name)
          {
            this.$Message.error({
              content : '??????????????????',
              duration: 2.6,
            });
            return;
          }
          if(!this.address)
          {
            this.$Message.error({
              content : '??????????????????',
              duration: 2.6,
            });
            return;
          }
          if(!this.contacts)
          {
            this.$Message.error({
              content : '?????????????????????',
              duration: 2.6,
            });
            return;
          }
          if(!this.number)
          {
            this.$Message.error({
              content : '??????????????????',
              duration: 2.6,
            });
            return;
          }

          $.ajax({
            url    : '/business/' + type,
            type   : 'POST',
            data   : {
              _id        : sId,
              Name       : this.name,
              Address    : this.address,
              Contacts   : this.contacts,
              Number     : this.number,
              CreateDate : this.createDate || new Date,
              Mark       : this.mark,
              BusinessPic: this.businessPic ||
              'http://pic5.40017.cn/02/000/4e/c1/rBLkCFjrQWeABrsrAAIAABd8uOQ790_640x_02.jpg',
              IsFaithful : this.subjectType,
            },
            success: function(res) {
              if (JSON.parse(res).ok - 0 === 1) {

                that.$Message.loading({
                  content : '????????????',
                  duration: 2.6,
                });
              }
              console.log(res);
            },
          });

        },
        handleSuccess(res, file) {
          this.businessPic = this.host + res.key;

          console.log(this.businessPic);
        },
      },
    };
</script>
<!-- styling for the component -->
<style>
    .option {
        border: 1px solid #e4e6e8;
        margin: 10px 0;
        padding: 5px 10px;
        border-radius: 2px;
    }

    .option-content li {
        padding: 7px 0;
        list-style: none;
    }

    .option-content li > i {
        font-style: normal;
        color: #1abc9b;
        margin-right: 5px;

    }

    ._br {
        height: 10px;
    }

    ._label {
        margin-right: 10px;
    }

    h3 {
        font-size: 14px;
        margin: 0;
        padding: 0 0 15px 0;
    }
</style>


