<template>
    <div>
        <section class="main-content-wrapper">
            <section id="main-content">

                <div id="subjectlist">
                    <h3>????????????</h3>
                    <div class="subject-list">
                        <table width="100%">
                            <thead>
                            <td width="70">??????</td>
                            <td width="100">?????????</td>
                            <td width="100">??????</td>
                            <td width="80">????????????</td>
                            <td width="70">?????????????????????</td>
                            <td width="80">??????</td>
                            <td width="80">??????</td>
                            <td width="100">??????</td>
                            </thead>
                            <tbody>
                            <tr v-for="item in subjectList">
                                <td>{{item.name}}</td>
                                <td>{{item.contacts}}</td>
                                <td>{{item.number}}</td>
                                <td>{{item.createTime}}</td>
                                <td v-if="item.subjectType=='1'" width="500px">???</td>
                                <td v-if="item.subjectType=='2'" width="500px">????????????</td>
                                <td>{{item.address}}</td>
                                <td>{{item.mark}}</td>
                                <td>
                                    <Button type="success" size="small" :disabled="item.added"
                                            v-on:click="link(item.id)">??????
                                    </Button>
                                    <Button type="error" size="small" v-on:click="deleteItem(item.id)">??????</Button>
                                </td>
                            </tr>
                            </tbody>
                        </table>
                    </div>
                </div>

            </section>
        </section>
    </div>
</template>
<script>

  //import RightBar from './Rightbar'

  export default {
    name   : 'subjectlist',
    data() {
      return {
        subjectList: [],
        rightBasket: [],
      };
    },
    mounted() {
      this.fetchList();
    },
    props  : {},
    //components: {RightBar},
    methods: {
      fetchList : function() {
        var that = this;

        $.ajax({
          url    : '/business/fetchlist',
          type   : 'GET',
          data   : {},
          success: function(res) {

            var data = JSON.parse(res);

            if (data.success - 0 === 1) {

              that.subjectList = data.list;
            }
          },
        });
      },
      deleteItem: function(id) {
        var that = this;

        $.ajax({
          url    : '/business/delete',
          type   : 'GET',
          data   : {
            id: id,
          },
          success: function(res) {

            var data = JSON.parse(res);

            if (data.success - 0 === 1) {
              that.$Message.loading({
                content : '????????????',
                duration: 2.6,
              });

              that.fetchList();
            }
          },
        });
      },
      link      : function(id) {
        location.assign('/business/editor?editor=1&id=' + id);
      },
      sortChange: function() {
      },
      submit    : function() {},
    },

  };
</script>
<!-- styling for the component -->
<style>

    .subject-list table {
        table-layout: fixed;
        overflow: hidden;
    }

    .subject-list tbody td:first-child {
        color: #666;
    }

    .subject-list thead td {
        border: 1px solid #e4e6e8;
        border-bottom: 1px solid #ccc;
        padding: 10px;
        white-space: nowrap;
        text-align: center;
        font-weight: 600;
    }

    .subject-list tbody tr:hover {
        background: #ebecef;
    }

    .subject-list td {
        border: 1px solid #e4e6e8;
        white-space: nowrap;
        text-align: center;
        padding: 5px 10px;
    }

    .subject-list tr td {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .option {
        border: 1px solid #e4e6e8;
        margin: 10px 0;
        padding: 5px 10px;
        border-radius: 2px;
    }

    .option-content li {
        padding: 7px 0;
        list-style: none;
    }

    .option-content li > i {
        font-style: normal;
        color: #1abc9b;
        margin-right: 5px;

    }

    td > em {
        font-style: normal;
        color: #1abc9b;
    }

    td > span {
        color: #333;
    }

    ._br {
        height: 10px;
    }

    ._label {
        margin-right: 10px;
    }

    h3 {
        font-size: 14px;
        margin: 0;
        padding: 0 0 15px 0;
    }
</style>


